export default {
	data() {
		return {
			id: '',
			info: ''
		}
	},
	onLoad(option) {
		console.log(option);
		this.id = option.id;
	},
	onShow() {
		this.getInfo();
	},
	methods: {
		getInfo() {
			var query = this.$Bmob.Query('product');
			query.get(this.id).then((res) => {
				this.info = {
					imgSrc: res.imgSrc,
					title: res.title,
					desc: res.desc,
					website: res.website
				}
			})
		}
	}
}
